<template>
  <div id="app">
    <div id="main">
      <van-search v-model="searchWords" show-action placeholder="搜索市、区、县等" @search="getInfo"  shape="round"  background="transparent" >
        <template #action>
         
          <van-button plain type="info" @click="getInfo" round size="small">搜索</van-button>
        </template>
      </van-search>

     
      <p id="txt-location"><van-icon name="location-o" /> {{city}}</p>
      <p id="txt-temperature">{{temp}}°</p>
      <p id="txt-weather">{{text}}</p>
      <p id="txt-wind">{{windDir}}</p>
    </div>
  </div>
</template>
<script>
  // import { Notify } from 'vant';
  export default {
    name: 'app',
    data() {
      return {
        searchWords: '',
        city: '眉山',
        // 温度
        temp: '6',
        // 风向
        windDir: '东北风',
        // 天气
        text: '晴'
      }
    },
    methods: {
      async getInfo() {
        let key = '0720a1b59c894f73a1e15cbcb3625e0f';
        let cityUrl =
          `https://geoapi.heweather.net/v2/city/lookup?location=${this.searchWords}&key=${key}`
        let {
          data: cityInfo
        } = await this.$axios.get(cityUrl)
        if (cityInfo.code != 200) {
          return this.$Notify({
            type: 'danger',
            message: '获取失败11'
          });
        }
        console.log(cityInfo)
        console.log(cityInfo.location[0].id)
        let httpUrl =
          `https://devapi.heweather.net/v7/weather/now?location=${cityInfo.location[0].id}&key=${key}`
        let {
          data: weatherInfo
        } = await this.$axios.get(httpUrl)
        console.log(weatherInfo)
        if (weatherInfo.code != 200) {
          return this.$Notify({
            type: 'danger',
            message: '获取失败'
          });
        }
        this.$Notify({
          type: 'success',
          message: '获取城市天气成功!'
        });
        this.temp = weatherInfo.now.temp
        this.windDir = weatherInfo.now.windDir
        this.text = weatherInfo.now.text
        this.city = this.searchWords
        this.searchWords = ''
      }
    }
  }
</script>
